<template lang="html">
  <div class="row">
    <div class="col-lg-8 col-md-8 col-sm-12">
      <form-card
        v-for="group of centerGroups"
        :group="group"
        v-model="form"
      ></form-card>
    </div>
    <div class="col-lg-4 col-md-4 col-sm-12">
      <div class="card">
        <div class="card-header">
          <h6 class="title">Phân quyền</h6>
        </div>
        <div class="card-body form-card">
          <div class="col-sm-12">
            <my-select
              class="full-width"
              placeholder="Phân quyền"
              :attribute="roles"
              v-model="form.role_id"
              :class="errors.has('role_id')?'border-danger':''"
              v-validate="'required'"
              :data-vv-name="role_id"
              data-vv-as="Phân quyền"
            >
            </my-select>
            <span class="text-danger" v-if="errors.has('role_id')">{{ errors.first('role_id') }}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="container-fluid">
      <div class="page-action">
        <el-button @click="cancel">Hủy</el-button>
        <el-button type="primary" @click="save">Lưu lại</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import FormCard from 'src/components/UIComponents/FormCard.vue'
import { Message, Input, Button } from 'element-ui';
import dataFrom from './user-form'
import MySelect from 'src/components/UIComponents/Select'

let _form = {}, firstGroups, secondGroups;

export default {
  components: {
    FormCard,
    ElInput: Input,
    ElButton: Button,
    MySelect
  },
  async beforeCreate() {

    firstGroups = dataFrom[0].groups;
    secondGroups = dataFrom[1].groups;

    firstGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })
    secondGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })

    _form['role_id'] = '';

    await this.$store.dispatch('fetchRoles');
  },
  computed: {
    roles() {
      let data = this.$store.state.roles;
      let arr = {};
      arr.options = [];
      for (let item of data) {
        arr.options.push({
          value: item.id,
          title: item.title
        });
      }
      return arr;
    },
  },
  data () {
    return {
      form: _form,
      centerGroups: firstGroups,
      sideGroups: secondGroups
    }
  },
  mounted() {
    this.form['role_id'] = 1;
    this.$store.dispatch('setPageTitle', 'Tạo tài khoản quản trị');
    this.$store.dispatch('setCurrentActions', [{
      label: 'Lưu lại',
      type: 'primary',
      icon: '',
      callback: this.save
    }])
  },
  methods: {
    cancel() {
      this.$router.push({name: 'AllUsers'});
    },
    save() {
      let self = this;
      this.$validator.validateAll().then(function (result) {
        if (result) {
          self.$store.dispatch('storeUser', self.form).then(res => {
            Message({
              message: 'Tạo mới thành công',
              type: 'success'
            });
            self.$router.push({name: 'AllUsers'})
          }, res => {
            Message({
              message: res.message,
              type: 'danger'
            });
          });
        }
      })
    }
  },
  destroyed(){
    this.$store.dispatch('setCurrentActions', []);
  }
}
</script>
